<script>
//import Pager from '../../components/Common/Pager.vue';
import Schema from "../Asset/DepreciationListSchema.js"
import DataService from '../../service/DataService';
export  default {


 name:'Depreciation',
 components:{
    
 },
 
    data() {
        return{
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            LegendArray: [],
            noRecordForTab:false,
            isLoading:false,
            DepreciationListData:[],
            widgets: [],
            Headers: [{
                "COLUMN_NAME": "itemTypeName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Item Type",
                "DISPLAY_ORDER": 1,
            },
            {
                "COLUMN_NAME": "itemCode",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Item Code",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "itemName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Item Name",
                "DISPLAY_ORDER": 3,
                
            },
            {
                "COLUMN_NAME": "uniqueName",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product Name",
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": "locationName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Location",
                "DISPLAY_ORDER": 5,
            },

            {
                "COLUMN_NAME": "usefulLife",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Useful Life In Year",
                "DISPLAY_ORDER": 6,
            },
            {
                "COLUMN_NAME": "percentageOfAssetLife",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Percentage Of Assets Life",
                "DISPLAY_ORDER": 7,
            },
            {
                "COLUMN_NAME": "depreciationMethodName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Depreciation Rule",
                "DISPLAY_ORDER": 8,
            },
            {
                "COLUMN_NAME": "deploymentDate",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Install Date",
                "DISPLAY_ORDER": 9,
            },
            {
                "COLUMN_NAME": "quantityOpening",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Opening Quantity",
                "DISPLAY_ORDER": 10,                
            },
            {
                "COLUMN_NAME": "quantityAddition",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Addition Quantity",
                "DISPLAY_ORDER": 11,
               
            },
            {
                "COLUMN_NAME": "QuantityDeletion",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Deletion Quantity",
                "DISPLAY_ORDER": 12,
                
            },
            {
                "COLUMN_NAME": "quantityClosing",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Closing Quantity",
                "DISPLAY_ORDER": 13,
                
            },
            {
                "COLUMN_NAME": "grossBlockOpening",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Opening",
                "DISPLAY_ORDER": 14,                
            },
            {
                "COLUMN_NAME": "grossBlockAddition",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Addition",
                "DISPLAY_ORDER": 15,
               
            },
            {
                "COLUMN_NAME": "grossBlockDeletion",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Deletion",
                "DISPLAY_ORDER": 16,
                
            },
            {
                "COLUMN_NAME": "grossBlockClosing",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Closing",
                "DISPLAY_ORDER": 17,
                
            },
            {
                "COLUMN_NAME": "balanceValue",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Balance Value",
                "DISPLAY_ORDER": 18,
               
            },
            {
                "COLUMN_NAME": "depreciationUptoPreviousMonth",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Depreciation Upto PreviousMonth",
                "DISPLAY_ORDER": 19,
                
            },
            {
                "COLUMN_NAME": "depreciationOnYTD",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Accumulated Depricationon YTD",
                "DISPLAY_ORDER": 20,
                
            },
            {
                "COLUMN_NAME": "currentBookValue",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Current Book Value",
                "DISPLAY_ORDER": 21,
                
            },

            ], 
            productName:'',
            LocationId:'',
            search:'',
            ProductTypeId:'',
            FromDate:'',
            ToDate:'',
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,          
            TotalRecords:0,
            CurrentPage: 1,
            TotalPages: 0,
            privilegeParams:null
        }
    },


    created:async function(){
        var vm=this;
        vm.privilegeParams = `controller=Asset&action=`; 
        await vm.CheckPrivilege(); 
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "ProductType");       
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "Location");        
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "DepreciationDate");
    },  
    methods:{  
        CheckPrivilege(){
             var vueObj=this;
            DataService.CheckPrivilege(vueObj.privilegeParams + 'Depreciation').then((response)=>{
            if (response.data) {
                    vueObj.GetDepreciationList();
                }else{
                    vueObj.ShowAlert(vueObj.$t('NotAuthorizedSection'), "failure",true, "Alert");
                }
            });
        },     
      async GetDepreciationList(){
            debugger
            var url=`productName=${this.productName}&searchCondition=${this.search}&fromDate=${this.FromDate}&toDate=${this.ToDate}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&location_id=${this.LocationId}&productTypeIds=${this.ProductTypeId}`;
            await DataService.GetDepreciationList(url).then(response=>{              
                if(response.data!=null){
                    if(response.data.length>0){                     
                         this.DepreciationListData=response.data;                    
                         this.TotalRecords = response.data[0].totalrecords;
                         this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
                         this.CurrentPage = this.PageNumber;
                         this.noRecord = false;
                    }
                    else{
                        this.noRecord = true;
                        this.DepreciationList = [];
                        this.TotalRecorded = 0
                    }                   
                }
            });

        },      
        pagerMethod: function (value) {
            debugger
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetDepreciationList();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                // switch (action.title) {
                //     case 'Edit':
                //         action.isVisible = true;
                //         break;
                // }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick(event) {
            debugger
            switch (event.callbackfunction) {
            case "BacktoList":
                this.OpenBacktoList();
            break;
            }

        },
        actionButtonClick() {

        },
        OpenBacktoList() {
            debugger
            var vm = this;
          
            vm.$router.push({ path: '/AssetCatalog/Index' });
        },


    }
  


}

</script>